.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.title {
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  gap: 8px;
  animation: fade-in .4s ease;
}

.title h3 {
  font-family: Outfit;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin: 0;
}

.title p {
  opacity: 0.7;
  margin: 0;
  line-height: normal;
}

.answers {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 8px;
  animation: fade-in .4s ease;
}

.answer label {
  display: flex;
  height: 64px;
  justify-content: center;
  align-items: center;
  gap: 16px;
  align-self: stretch;
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.16);
  background: rgba(21, 21, 21, 0.40);
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  user-select: none;
  transition: background-color 200ms ease-out, border-color 200ms ease-out;
  animation: fade-in .4s ease-out;
}

.answer input {
  display: none;
}

.answer input:checked + label {
  border-color: #9362FF;
  background-color: rgba(147, 98, 255, 0.08);
}

.inputLabel {
  display: flex;
  align-items: center;
  gap: 4px;
}

.imageContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

.image {
  height: 100%;
  border-radius: 16px;
  animation: fade-in .4s ease-out;
}

@media screen and (max-width: 767px) {
  .title h3 {
    font-size: 24px;
  }

  .image {
    max-width: 100%;
    width: auto;
    height: calc(100dvh - 250px);
    object-fit: cover;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
